
import Progress from "@/components/progress";
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from "@/components/scrollAll";
import { state, classify } from "@/common/js/classify";
export default {
data() {
return {
dataList: [],
url: "",
params: {
province: "",
custState: "",
custType: "",
maturityType: "",
custLevel: "",
custFrom: ""
},
page: 1,
count: 10,
total: 0,
dataList: [],
queryType: 1,
startDate: "",
endDate: "",
selUserId: 0,
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
classify: classify
};
},
mounted() {
let query = this.$route.query;
this.params[state[query.sIndex].param] = query.param;
this.queryType = query.queryType;
this.startDate = query.startDate;
this.endDate = query.endDate;
this.selUserId = query.selUserId;
this.getData(1, 1);
},
methods: {
getData(type, page) {
this.$vux.loading.show({
text: "加载中"
});

this.page = page || this.page;
let params = {};
for (let key in this.params) {
if (this.params[key]) {
params[key] = this.params[key];
}
}
this.getAxios("/CustStatis/GetCustStatisList", {
...params,
page: this.page,
count: this.count,
queryType: this.queryType,
startDate: this.startDate,
endDate: this.endDate,
selUserId: this.selUserId
})
.then(res => {
this.$vux.loading.hide();
if (res.Data.List.length > 0) {
if (type === 2) {
this.dataList = [...this.dataList, ...res.Data.List];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.dataList = res.Data.List;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.scrollTo(0, 0);
this.$refs.scroll.forceUpdate(true);
}
this.page += 1;
this.$refs.scroll.refresh();
} else if (type == 1) {
this.dataList = [];
this.$refs.scroll.forceUpdate();
this.$refs.scroll.scrollTo(0, 0);
} else {
this.$refs.scroll.forceUpdate();

}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});
},
getLastMoney(item) {
return (item.TotalPrice - item.TotalReturnMeony).toFixed(2);
},
ratio(item) {
let moneyRatio =
item.TotalReturnMeony / item.TotalPrice != NaN
? (item.TotalReturnMeony / item.TotalPrice).toFixed(2) * 100 + "%"
: "";
if (moneyRatio == "100%" && item.TotalPrice - item.TotalReturnMeony > 0) {
moneyRatio = "99%";
}
return moneyRatio;
}
},
filters: {
time(day) {
return dayjs(day).format("YYYY-MM-DD");
},
schedule(count, total) {
if (!count) {
return 0;
} else {
return Number.parseFloat(((count / total) * 100).toFixed(2));
}
},
getPicSrc(id) {
return `${apiPic}/dXNlci9sb2dv_${id}_35x35.png`;
}
},
computed: {
queryName() {
return this.$route.query.name === "订单";
}
},
components: {
XHeader,
Scroll,
Progress
}
};



